.t{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #999;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.t-icon
{
    width: 15px;
    height: 15px;

}
.t-bt
{
    width: 25px;
    height: 25px;
    border-radius:50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
    left: 25px;
}
.t-sw
{
    width: 100px;
    font-size: 10px;
    font-weight: 200;
    position: absolute;
    left: 3px;
    top: 30px;

}


